<template>

  <div id="videoS" ref="videoS">
    <div class="videoS_title">{{data.title}}</div>
    <div class="videoS_time">{{dataFormat(data.time)}} {{data.views}}阅读</div>
    <div class="videoS_video">
      <video class="video" src="../../../assets/炎炎消防队-17.mp4" controls="controls"></video>
    </div>
    <div class="videoS_content">{{data.content}}</div>
    <my-comment-area :comment_data="comment_data" :bottomShow="bottomShow"
                     :CommentSubmit="CommentSubmit"></my-comment-area>
    <my-comment @clickC="sentC" @update="update"></my-comment>
  </div>
</template>

<script>
import myComment from '../../../components/myComment'
import myCommentArea from '../../../components/myCommentArea'

import myUtil from '../../../common/myUtil'

let VideoS
export default {
  name: 'videoS',
  components: {
    myComment,
    myCommentArea
  },
  data () {
    return {
      data: this.$route.params.videoS_data,
      CommentSubmit: 0,
      currentPage: 0,
      pageSize: 3,
      comment_data: [
        {
          name: '用户名用户名',
          text: '评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容',
          time: '12-11 12:34',
          portrait: ''
        }, {
          name: '用户名用户名',
          text: '评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容',
          time: '10-09 12:34',
          portrait: ''
        }, {
          name: '用户名用户名',
          text: '评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容',
          time: '10-09 12:34',
          portrait: ''
        }
      ],
      bottomShow: false
    }
  },
  mounted () {
    this.init()
  },
  destroyed () {
    VideoS.removeEventListener('scroll', this.scroll, false)
  },
  methods: {
    init () {
      VideoS = this.$refs.videoS
      VideoS.addEventListener('scroll', this.scroll, false)
    },
    dataFormat (time) {
      return myUtil.formatTime(time)
    },
    scroll () {
      console.log('你进行了滑动')
      console.log(VideoS.scrollTop, VideoS.clientHeight, VideoS.scrollHeight)
      let nowH = VideoS.scrollTop + VideoS.clientHeight
      let maxH = VideoS.scrollHeight
      if (nowH + 1 > maxH) {
        this.$Dialog.Loading({})
        setTimeout(() => {
          this.query()
        }, 2000)
      }
    },
    query () {
      let time = myUtil.getTime('MM-DD HH:mm')
      console.log(time)
      let newVal = {
        name: '用户名用户名',
        text: '评论内容评论内容评论内容评论内容评论内容评论内容评论内容评论内容',
        time: time,
        portrait: ''
      }
      this.$Dialog.Loading({ state: 'end' })
      // 这个判断更改为获取的数据长度为0
      if (this.currentPage > 3) {
        console.log('你已经滑到底了')
        this.bottomShow = true
      } else {
        this.comment_data.push(newVal)
        this.currentPage += 1
      }
    },
    update (val) {
      let message = {
        name: '用户名用户名',
        text: val,
        time: '10-09 12:34',
        portrait: ''
      }
      this.comment_data.push(message)
      this.CommentSubmit = new Date().getTime()
    },
    sentC (value) {
      console.log('你进行的评论，评论的内容为:' + value)
    }
  }
}
</script>

<style scoped>
  #videoS {
    position: fixed;
    bottom: 3.333rem;
    top: 3rem;
    width: 100%;
    left: 0;
    overflow-y: auto;
  }

  #videoS .videoS_title {
    font-size: 1.3rem;
    color: black;
    padding: 1rem;
  }

  #videoS .videoS_time {
    color: black;
    padding: 0 1rem;
  }

  #videoS .videoS_video {
    padding: 1rem;
    height: 12rem;
  }

  #videoS .videoS_video .video {
    width: 100%;
    height: 100%;
  }

  #videoS .videoS_content {
    padding: 1rem;
  }

</style>



